import React, { useState, useEffect, Fragment, } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from 'react-router-dom';
import Header from '../components/Header';
import Leftnav from '../components/Leftnav';
import Rightchat from '../components/Rightchat';
import Appfooter from '../components/Appfooter';
import Popupchat from '../components/Popupchat';
import MyMarker from '../components/MyMarker';
import GoogleMapReact from 'google-map-react';
import api from '../utils/api';
import { useLocation } from 'react-router-dom';
import "./marker.css";

const eventList = [
    {
        imageUrl: 'hotel.png',
        title: 'Right here Right Now -  Comedy ',
        location: 'Goa, Mumbai',
        date: '22',
        month: 'FEB',
    },
]

const Event = () => {

    const [state, setstate] = useState({
        center: {
            lat: +55.927280555555555,
            lng: -3.209088888888889
        },
        zoom: 1
    });
    const [myTweets,setMyTweets]=useState([]);
    const {
        user, vcoin, isauthenticated
    } = useSelector(state => state.auth); // here, indicate reducer

    useEffect(() => {
        
        const setblogs = async()=>{
            const res1 = await api.get('/profile/');
            let followings = res1.data.following;
            const res= await api.post('/blog/myfeed', {myfollowing:followings});
            setMyTweets(res.data);
        }
        if(isauthenticated) setblogs();
    },[isauthenticated]);

    const location = useLocation();
    const hashtag = location.hash + "</a>";
    useEffect(() => {
        const setblogs = async()=>{
            const res= await api.post(`/blog/byhashtag/`, {hashtag:hashtag});
            setMyTweets(res.data);
        }
        if(isauthenticated) setblogs();
        
    },[hashtag]);
    
        return (
            <Fragment> 
                <Header />
                <Leftnav />
                {/* <Rightchat /> */}

                <div className="main-content right-chat-active">
                    <div className="middle-sidebar-bottom">
                        <div className="middle-sidebar-left pe-0">
                            <div className="row">
                                <div className="col-xl-12">
                                    <div className="card w-100 border-0 shadow-none rounded-xxl border-0 mb-3 overflow-hidden ">
                                        <div style={{ height: '400px', width: '100%' }}>
                                            <GoogleMapReact
                                            bootstrapURLKeys={{ key: 'AIzaSyCAa8ZgPpTBMcCV7lrNJXvE70JoRs9Wl8I',                                                     
                                                language: "en",
                                                region: "US"
                                            }}
                                            defaultCenter={state.center}
                                            defaultZoom={state.zoom}               
                                            >
                                                {
                                                    myTweets.map((Tweet,i)=> {
                                                    return (
                                                    <MyMarker
                                                        key={i}
                                                        lat={Tweet.lat}
                                                        lng={Tweet.lng}
                                                        data={Tweet}
                                                        creator={Tweet.creator}
                                                        user={user}
                                                        text={i}
                                                    />)
                                                })} 
                                            </GoogleMapReact>
                                        </div>
                                    </div>
                                </div>

                                {eventList.map((value , index) => (
                                <div key={index} className="col-lg-4 col-md-6 pe-2 ps-2">
                                    <div className="card p-3 bg-white w-100 hover-card border-0 shadow-xss rounded-xxl border-0 mb-3 overflow-hidden ">
                                        <div className="card-image w-100">
                                            <img src={`assets/images/${value.imageUrl}`} alt="event" className="w-100 rounded-3" />
                                        </div>
                                        <div className="card-body d-flex ps-0 pe-0 pb-0">
                                            <div className="bg-greylight me-3 p-3 border-light-md rounded-xxl theme-dark-bg"><h4 className="fw-700 font-lg ls-3 text-grey-900 mb-0"><span className="ls-3 d-block font-xsss text-grey-500 fw-500">{value.month}</span>{value.date}</h4></div>
                                            <h2 className="fw-700 lh-3 font-xss">{value.title}
                                                <span className="d-flex font-xssss fw-500 mt-2 lh-3 text-grey-500"> <i className="ti-location-pin me-1"></i>{value.location} </span>
                                            </h2>
                                        </div>
                                        <div className="card-body p-0">
                                            <ul className="memberlist mt-4 mb-2 ms-0 d-inline-block">
                                                <li><Link to="/defaultevent"><img src="assets/images/user.png" alt="user" className="w30 d-inline-block" /></Link></li>
                                                <li><Link to="/defaultevent"><img src="assets/images/user.png" alt="user" className="w30 d-inline-block" /></Link></li>
                                                <li><Link to="/defaultevent"><img src="assets/images/user.png" alt="user" className="w30 d-inline-block" /></Link></li>
                                                <li><Link to="/defaultevent"><img src="assets/images/user.png" alt="user" className="w30 d-inline-block" /></Link></li>
                                                <li className="last-member"><Link to="/defaultevent" className="bg-greylight fw-600 text-grey-500 font-xssss ls-3 text-center">+2</Link></li>
                                            </ul>
                                            <Link to="/defaultevent" className="font-xsssss fw-700 ps-3 pe-3 lh-32 float-right mt-4 text-uppercase rounded-3 ls-2 bg-success d-inline-block text-white me-1">APPLY</Link>
                                        </div>
                                    </div>
                                </div>
                                ))}

                            </div>
                        </div>
                    </div>
                </div>

                <Popupchat />
                <Appfooter /> 

            </Fragment>
        );
}

export default Event;