import React, { useState, useEffect } from "react";
import { format, formatDistance } from "date-fns";
import api from "../utils/api";

const MyMarker = ({ text, tooltip, data, creator }) => {
  const [isActive, setActive] = useState("true");
  const handleToggle = () => {
    setActive(!isActive);
  };
  const [creatorName, setUsername] = useState("");
  useEffect(() => {
    const showusername = async () => {
      const res = await api.post("/auth/name", { id: creator });
      setUsername(res.data);
    };
    showusername();
  }, []);

  return (
    <div  className={isActive ? "circle" : "circle active"} onClick={handleToggle}>
      <span className="circleText" title={tooltip}>
        {text + 1}
      </span>
      <div className="circle-detail">
        <div className="text-center">
          <label className="fw-900 font-xss">{creatorName}</label>
          <span className="mentiontag fw-900 font-xss">@{creatorName}</span>
        </div>
        <div className="circle-subdetail">
          <p dangerouslySetInnerHTML={{__html: data.description}}></p>
          <p className="date">{formatDistance(new Date(data.date), new Date(), { addSuffix: true })}</p>
        </div>
      </div>

    </div>
  );
};

export default MyMarker;
